html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
details,
section,
summary,
aside,
main,
menu,
nav,
figcaption,
figure,
footer,
header,
hgroup {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
b,
strong {
  font-weight: bold;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
  cursor: pointer;
  -webkit-appearance: button;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type='checkbox'],
input[type='radio'] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  height: auto;
}
input[type='search'] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
.container {
  position: relative;
  height: 100vh;
  overflow: hidden;
  lost-utility: clearfix;
}
@media only screen and (max-width: 480px) {
  .container {
    padding-bottom: 250px;
    overflow: scroll;
    height: auto;
  }
}
.container.hide-drawer .drawer {
  lost-column: 0/12 12 0;
  overflow: hidden;
}
.container.hide-drawer .main-content {
  lost-column: 12/12 12 0;
}
.drawer {
  font-size: 0.8rem;
  -webkit-transition: 250ms;
  -moz-transition: 250ms;
  -o-transition: 250ms;
  -ms-transition: 250ms;
  transition: 250ms;
  lost-column: 4/12 12 0;
  height: 100vh;
  overflow: auto;
}
@media only screen and (max-width: 768px) {
  .drawer {
    font-size: 0.6rem;
  }
}
@media only screen and (max-width: 480px) {
  .drawer {
    lost-column: 1/1 1 0;
    overflow: visible;
    height: auto;
  }
}
.lower-section {
  background-color: #fafafa;
}
.lower-section ul.links {
  margin: 0 0 3rem 0;
  padding: 0;
  list-style: none;
}
.lower-section ul.links li {
  cursor: pointer;
  padding: 20px;
  border: 1px solid #dad9da;
}
.lower-section ul.links li a {
  font-size: 1rem;
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
  width: 100%;
  padding: 1rem;
  padding-left: calc(11% + 4rem);
  padding-right: 5px;
  color: #646261;
  font-weight: 700;
}
@media only screen and (max-width: 768px) {
  .lower-section ul.links li a {
    font-size: 0.6rem;
  }
}
.lower-section ul.links li a:before {
  content: "";
  position: absolute;
  left: 11%;
  top: 0.9rem;
  width: 2rem;
  height: 2rem;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  background-size: contain;
}
.lower-section ul.links li a.aboutme:before {
  background-image: url("../images/aboutme.png");
}
.lower-section ul.links li a.education:before {
  background-image: url("../images/education.png");
}
.lower-section ul.links li a.work:before {
  background-image: url("../images/experience.png");
}
.lower-section ul.links li a.skills:before {
  background-image: url("../images/skills.png");
}
.main-content {
  font-size: 1rem;
  lost-column: 8/12 12 0;
  height: 100vh;
  overflow: auto;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px 5%;
}
@media only screen and (max-width: 768px) {
  .main-content {
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 480px) {
  .main-content {
    lost-column: 1/1 1 0;
    overflow: visible;
    height: auto;
  }
}
.main-content #menu img {
  width: 3rem;
  height: 3rem;
}
.main-content h2,
.main-content h3 {
  color: #838383;
}
.main-content h1 {
  margin: 30px 0;
  font-size: 1.4rem;
}
.main-content .about-list li {
  margin-bottom: 1rem;
}
.main-content .about-list li:before {
  content: '\2666';
  margin-right: 15px;
}
.main-content .languages {
  margin: 1.8rem 0;
  lost-utility: clearfix;
}
.main-content .languages img {
  lost-column: 1/3 0;
  max-width: 140px;
}
.main-content .world-map {
  margin: 2rem 0;
  lost-utility: clearfix;
  border: 1px solid #dad9da;
  -webkit-box-shadow: 7px 3px 20px 2px #dad9da;
  box-shadow: 7px 3px 20px 2px #dad9da;
}
.main-content .world-map img {
  lost-column: 1/1;
}
.main-content .places span {
  padding: 1.5rem;
  position: relative;
}
.main-content .places span.living:before {
  content: "";
  position: absolute;
  left: 0;
  width: 1.5rem;
  top: 1.39rem;
  height: 1.5rem;
  background-image: url("../images/red-marker.png");
  background-repeat: no-repeat;
}
.main-content .places span.visited:before {
  content: "";
  position: absolute;
  left: 0;
  width: 1.5rem;
  height: 1.5rem;
  background-image: url("../images/blue-marker.png");
  background-repeat: no-repeat;
}
.main-content .skillset li {
  text-align: center;
  display: inline-block;
  white-space: nowrap;
  margin: 3%;
  padding-left: 2rem;
  position: relative;
}
.main-content .skillset li:before {
  content: "";
  position: absolute;
  left: 0;
  top: -0.2rem;
  width: 1.5rem;
  height: 1.5rem;
  background-image: url("../images/check.png");
  -webkit-background-size: contain;
  -moz-background-size: contain;
  background-size: contain;
  background-repeat: no-repeat;
}
.profile {
  background-color: #f0f0f0;
  text-align: center;
}
.profile h1 {
  text-transform: uppercase;
  margin-bottom: 0;
}
.profile h2.role {
  color: #646261;
  margin-top: 0.1rem;
}
.profile .picture {
  position: relative;
}
.profile .picture img {
  width: 65%;
  border-radius: 50%;
  margin: 4.5rem 18% 2rem;
}
.profile .picture .countryflag {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 14%;
  border: 0.5rem solid #f0f0f0;
}
.profile .country {
  font-size: 0.8rem;
  position: relative;
  color: #646261;
  font-weight: normal;
  padding-left: 1.5rem;
  font-style: italic;
  display: inline-block;
  margin-top: 1rem;
  font-weight: 100;
}
.profile .country:before {
  content: "";
  position: absolute;
  left: 0;
  top: -0.1rem;
  width: 1.5rem;
  height: 1.5rem;
  background-image: url("../images/country.png");
  background-repeat: no-repeat;
}
.profile .cells {
  lost-utility: clearfix;
  height: 80px;
}
.profile .cells .cell {
  padding: 20px 0;
  height: 100%;
  lost-column: 1/3 3 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-right: 1px solid #dad9da;
  border-top: 1px solid #dad9da;
  border-bottom: 1px solid #dad9da;
  text-align: center;
}
.profile .cells .cell span {
  vertical-align: middle;
}
.profile .cells .cell span strong {
  font-size: 1rem;
}
.profile .cells .cell:last-child {
  border-right: none;
}
footer.footer {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 8%;
  font-size: 1rem;
}
@media only screen and (max-width: 992px) {
  footer.footer {
    font-size: 0.6rem;
  }
}
@media only screen and (max-width: 600px) {
  footer.footer {
    font-size: 0.4rem;
  }
}
@media only screen and (max-width: 480px) {
  footer.footer {
    font-size: 0.8rem;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 5%;
    width: 100%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background-color: #f0f0f0;
  }
}
footer.footer ul {
  list-style: none;
  padding: 0;
}
footer.footer ul li a {
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  color: #646261;
  padding: 0.3em 0 0 1.5rem;
  text-decoration: none;
}
footer.footer ul li a:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.3rem;
  width: 1.2rem;
  height: 1.2rem;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  background-size: contain;
}
@media only screen and (max-width: 768px) {
  footer.footer ul li a:before {
    width: 1rem;
    height: 1rem;
    top: 0.1rem;
  }
}
footer.footer ul li a.mail:before {
  background-image: url("../images/mail.png");
}
footer.footer ul li a.linkedin:before {
  background-image: url("../images/linkedin.png");
}
footer.footer ul li a.github:before {
  background-image: url("../images/git.png");
}
body {
  font-family: 'Montserrat', sans-serif;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
